.ReleaseNotes {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  word-wrap: break-word;
}

.ReleaseNotes img {
  max-width: 100%;
}

.ReleaseNotes ol,
.ReleaseNotes ul {
  padding-left: 2em;
}

.ReleaseNotes blockquote,
.ReleaseNotes details,
.ReleaseNotes dl,
.ReleaseNotes ol,
.ReleaseNotes p,
.ReleaseNotes pre,
.ReleaseNotes table,
.ReleaseNotes ul {
  margin-bottom: 16px;
  margin-top: 0;
}

.ReleaseNotes h1,
.ReleaseNotes h2,
.ReleaseNotes h3,
.ReleaseNotes h4,
.ReleaseNotes h5,
.ReleaseNotes h6 {
  font-weight: 600;
  line-height: 1.25;
  margin-bottom: 16px;
  margin-top: 24px;
}

.ReleaseNotes > :first-child,
.ReleaseNotes > div > :first-child {
  margin-top: 0 !important;
}

.ReleaseNotes > :last-child,
.ReleaseNotes > div > :last-child {
  margin-bottom: 0 !important;
}

